import React, { Component } from 'react';
import { Link } from "react-router-dom";
import Card from "../Components/Card";

import Footer from "../Components/Footer";
import AboutUs from "../Components/AboutUs";

import HeroImage from "../Images/Oxararfoss-full.jpeg";
import EliteLogoImage from "../Images/logo.jpg";

class Home2 extends Component {
  constructor(props)
  {
    super(props);
    this.state = {
      tours: []
    }
  }

  componentDidMount(){
    var that = this;
    fetch('http://34.76.27.1:1337/api/tours', {mode: 'cors'})
    .then(function(response) {
      return response.json();
    }).then(function(myJson) {
      that.setState({tours: myJson});
    });
  }

  render() {
    return (
      <div className="HomePage">
        <div className="Navbar2">
          <img src={EliteLogoImage} className="LogoImage" alt="Logo"></img>
        </div>
        <div className="FluidContainer">
          <img src={HeroImage} className="HeroImage" alt="Hero"></img>
          <h1 className="HeroTitle">
          Come and explore <br/>
          <span style={{fontSize:"2em", color: "#90caf9"}}>ICELAND</span> <br/>
          Adventures, amazing sights and untouched nature awaits you! <br/>
          <a href="#tours">
            <button className="blue">
              Browse our selection of tours.
            </button>
          </a>
          <a href="#about">
            <button className="white">
              Learn more about us!
            </button>
          </a>
          </h1>
        </div>
        <div className="TourList" id="tours">
          {
            this.state.tours.map(x=> 
              <Link to={"/tour/"+x.id} key={x.id}><Card title={x.name} image={x.img} ></Card></Link>
            )
          }
        </div>
        <AboutUs />
        <Footer />
      </div>
    );
  }
}

export default Home2;
