import React, { Component } from 'react';

import SocialMedia from "./SocialMedia";
import TourOperatorImage from "../Images/tour_operator.png";

class Footer extends Component {
  render() {
    return (
    <div id="footer" className={"Footer " + this.props.className}>

    <img alt="Tour Operator" style={{width:"25vw",height:"25vw",maxWidth:192,maxHeight:192,minWidth:64,minHeight:64,marginLeft:"auto",marginRight:"auto",display:"flex"}} src={TourOperatorImage}></img>

      <SocialMedia />
    </div>
    );
  }
}

export default Footer;