import React, { Component } from 'react';
import { Link } from "react-router-dom";

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookSquare, faTwitterSquare, faYoutubeSquare } from '@fortawesome/free-brands-svg-icons'
import { faPhoneSquare, faEnvelopeSquare, faQuestionCircle } from '@fortawesome/free-solid-svg-icons'

class SocialMedia extends Component {
  render() {
    return (
    <div className="SocialMedia">
      <Link to="http://facebook.com/elitetraveliceland"><FontAwesomeIcon className="FacebookIcon" icon={faFacebookSquare} /></Link>
      <Link to="http://twitter.com/elitetraveliceland"><FontAwesomeIcon className="TwitterIcon" icon={faTwitterSquare} /></Link>
      <Link to="http://youtube.com/elitetraveliceland"><FontAwesomeIcon className="YoutubeIcon" icon={faYoutubeSquare} /></Link>
      <a href="mailto:concierge@elitetravel.is"><FontAwesomeIcon className="EmailIcon" icon={faEnvelopeSquare} /></a>
      <a href="tel:003548888889"><FontAwesomeIcon className="PhoneIcon" icon={faPhoneSquare} /></a>
      <Link to="/help"><FontAwesomeIcon className="GreenpediaIcon" icon={faQuestionCircle} /></Link>
    </div>
    );
  }
}

export default SocialMedia;