import React, { Component } from 'react';

class Card extends Component {
  render() {
    return (
    <div className="Card">
        <div className="image-container">
          <img src={this.props.image} alt="card" />
          <div className="overlay"></div>
        </div>
        <div className="CardTitle">
            {this.props.title}
        </div>
    </div>
    );
  }
}

export default Card;